import React from 'react'
import {Card, Row, Col, Table,Button} from 'antd'
import {withRouter} from 'react-router-dom'
// import SearchForm from './searchForm'
import Filter from './Filter'
import '../index.less'
import CareModal from './careModal'
import CancelCareModal from './cancelCareModal'
import {setLocal} from '../../../utils'
import {postAction} from "../../../axios";

import {hostUrl} from '../../../env'
class Pinkunshengchaxun extends React.Component {
    constructor() {
        super()
        this.state = {
            dataSource: [
                // {
                //     key: "1",
                //     sortIndex: "1",
                //     studentNo: "U20136789",
                //     studentName: "龙祥",
                //     collegeName: "机电工程学院",
                //     majorName: "模具设计与制造",
                //     className: "模具1301",
                //     yearName:"2018级",
                //     poorLevel:"一级困难",
                //     funding:"",
                //     fundingTime:"2018-07-01",
                //     hasCare:0,
                //     fundLevel:'一级助学金',
                //     reason:'确认家庭经济困难学生'
                // }
            ],
            pagination: {
                current: 1, // 当前页数，
                pageSize: 10, // 每页条数
                total: 0, // 数据总条数
                showTotal: (total) => `共${total}条数据`, //显示数据总条数
                showQuickJumper: true // 显示页码快速跳转
            },
            careModalVisible: false,
            cancelCareModalVisible: false,
            filterValue: {},
            studentInfo: {}
        };
        this.getData = this.getData.bind(this);
        this.getFilterValueAndName = this.getFilterValueAndName.bind(this)
    }

    getData() {
        const {filterValue: {curGrade, curCollege, curMajor, curClass, studentNo, curPinkun, curGuanzhu, curZizhu,curYear}, pagination: {current, pageSize}} = this.state
        postAction(
            "/bigdata/poverty/povertyStudentInfoList",
            {
                schoolYear:curYear,
                grade: curGrade,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass,
                povertyLevel: curPinkun,
                lastSubsidizeType: curZizhu,
                attention: curGuanzhu,
                studentNo: studentNo,
                pageInfo: {
                    pageNum: current,
                    pageSize: pageSize
                }
            }
        ).then((res) => {
            if (res.obj.list && res.obj.list.length > 0) {
                let newData = [];
                res.obj.list.forEach(
                    (item, index) => {
                        newData.push({
                            key: index + 1,
                            sortIndex: index + 1,
                            studentId: item.studentId,
                            studentNo: item.studentNo,
                            studentName: item.studentName,
                            collegeName: item.collegeName,
                            majorName: item.majorName,
                            className: item.className,
                            yearName: item.grade,
                            poorLevel: item.povertyLevel,
                            funding: item.lastSubsidize,
                            fundingTime: item.lastSubsidizeDate,
                            hasCare: Number(item.attention),
                            fundLevel: item.lastSubsidizeType,
                            reason: item.attentionReason,
                            abnormityIndex:item.abnormityIndex,
                            schoolYearSubsidize:item.schoolYearSubsidize,
                            majorCode: item.majorCode
                        })
                    }
                )
                this.setState({
                    dataSource: newData,
                    pagination: {
                        current: res.obj.pageNum, // 当前页数，
                        pageSize: res.obj.pageSize, // 每页条数
                        total: res.obj.total, // 数据总条数
                        showTotal: (total) => `共${total}条数据`, //显示数据总条数
                        showQuickJumper: true // 显示页码快速跳转
                    },
                })
            } else {
                this.setState({
                    dataSource: [],
                    pagination: {
                        current: 1, // 当前页数，
                        pageSize: 10, // 每页条数
                        total: 0, // 数据总条数
                        showTotal: (total) => `共${total}条数据`, //显示数据总条数
                        showQuickJumper: true // 显示页码快速跳转
                    },
                })
            }
        })
    }

    handleTableChange = (pagination) => {
        this.setState(
            () => {
                return {
                    pagination: {
                        current: pagination.current, // 当前页数，
                        pageSize: pagination.pageSize, // 每页条数
                        total: pagination.total, // 数据总条数
                        showTotal: (total) => `共${total}条数据`,
                        showQuickJumper: true // 显示页码快速跳转
                    }
                }
            },
            () => {
                this.getData();
            }
        )
    }

    getFilterValueAndName(obj) {
        this.setState(
            {
                filterValue: obj
            },
            () => {
                this.getData()
            }
        )
    }

    componentDidMount() {
        this.getData()
    }

    //打开关注的弹窗
    handleCareMocalOpen = (value) => {
        this.setState({
            careModalVisible: true,
            studentInfo: value
        })
    }

    //打开取消关注的弹窗
    handlecancelcareModalOpen = (value) => {
        this.setState({
            cancelCareModalVisible: true,
            studentInfo: value
        })
    }

    //关闭关注的弹窗
    hidecareModal = (obj) => {
        if (obj === undefined) {
            this.setState({
                careModalVisible: false
            });
            return;
        }
        let {attentionReason, attentionRemark} = obj;
        const {filterValue: {curYear}} = this.state
        let params = {
            "studentNo": this.state.studentInfo.studentNo,
            "attentionReason": attentionReason,
            "attentionRemark": attentionRemark,
            "schoolYear":curYear
        };
        postAction(
            "/bigdata/poverty/addAttention",
            params
        ).then((res) => {
            if (res.success) {
                this.setState({
                    careModalVisible: false
                });
                this.getData();
            }
        });
    }

    //关闭取消关注的弹窗
    hidecancelcareModal = (obj) => {
        if (obj === undefined) {
            this.setState({
                cancelCareModalVisible: false
            });
            return;
        }
        let {attentionReason, attentionRemark} = obj;
        const {filterValue: {curYear}} = this.state
        let params = {
            "studentNo": this.state.studentInfo.studentNo,
            "attentionReason": attentionReason,
            "attentionRemark": attentionRemark,
            "schoolYear":curYear
        };
        postAction("/bigdata/poverty/cancelAttention", params).then((res) => {
            if (res.success) {
                this.setState({
                    cancelCareModalVisible: false
                });
                this.getData();
            }
        });
    };

    handleSubmit = (obj) => {
        // console.log(obj)
    }

    gotoDetail = (obj) => { //跳转详情页面\

        let data = {
            ...obj,
            backUrl: '/mainframe/jinzhunzizhu/pinkunshengchaxun',
            filterValue:this.state.filterValue,
        }
        // 存储点击的列表信息到localstorage
        setLocal('jzzz', JSON.stringify(data))
        this.props.history.push(
            {
                pathname: '/mainframe/jinzhunzizhu/detail',
                state: {
                    studentNo: obj.studentNo,
                    majorCode: obj.majorCode
                }
            }
        )

    }
    handleExport=()=>{
        let data=this.state.filterValue
        window.location.href=`${hostUrl}/bigdata/poverty/exportPovertyStudentInfoList?schoolYear=${data.curYear}&collegeCode=${data.curCollege}&majorCode=${data.curMajor}&grade=${data.curGrade}&classCode=${data.curClass}&povertyLevel=${data.curPinkun}&lastSubsidizeType=${data.curZizhu}&attention=${data.curGuanzhu}&studentNo=${data.studentNo}`
    }
    render() {
        const columns = [
            {title: '序号', dataIndex: 'sortIndex', width: "4%"},
            {
                title: '姓名', dataIndex: 'studentName', width:"6%", render: (text, record) => {
                    return (
                        <span onClick={() => {
                            this.gotoDetail(record)
                        }} style={{
                            display: 'block',
                            width: '100%',
                            height: '100%',
                            cursor: 'pointer',
                            color: '#1890ff'
                        }}>{text}</span>
                    )
                }
            },
            {
                title: '学号', dataIndex: 'studentId', width:"7%", render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '一卡通号', dataIndex: 'studentNo', width:"7%", render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '年级', dataIndex: 'yearName', width:"5%", render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '院系', dataIndex: 'collegeName', render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '专业', dataIndex: 'majorName', render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '班级', dataIndex: 'className', width:"6%", render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '认定等级', dataIndex: 'poorLevel', width:"6%", render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '经济困难指数', dataIndex: 'abnormityIndex', width:"5%", render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '最近一次资助类型', dataIndex: 'fundLevel', width:"7%", render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '最近一次资助金额（元）', dataIndex: 'funding', width:"8%", render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '最近一次资助时间', dataIndex: 'fundingTime', width:"8%", render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '学年资助金额（元）', dataIndex: 'schoolYearSubsidize', width:"7%", render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '重点关注', dataIndex: 'hasCare', width: '6%', align: 'center',
                render: (text, record) => {
                    return (
                        text === 0 ?
                            <span onClick={() => {
                                this.handleCareMocalOpen(record)
                            }} style={{cursor: "pointer", color: "#1890ff"}}>
						加关注
					</span> : <span onClick={() => {
                                this.handlecancelcareModalOpen(record)
                            }}
                                    style={{cursor: "pointer", color: "#f04864"}}>
						取消关注
					</span>

                    )
                }
            },
            {
                title: '关注原因', dataIndex: 'reason', width:"8%", render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
        ];
        return (
            <React.Fragment>
                <Row style={{margin: '-20px'}}>
                    <Col span={24} className="filter-jingzhun">
                        <Filter getFilterValueAndName={this.getFilterValueAndName}/>
                    </Col>
                </Row>
                <Card
                    style={{marginTop: 40}}
                    title='家庭经济困难学生查询'
                    headStyle={{border: 'none', fontSize: '16px'}}
                    bodyStyle={{paddingTop:0}}
                    extra={
                        <Button type='primary' onClick={this.handleExport}>导出</Button>
                    }
                >

                    <Table
                        dataSource={this.state.dataSource}
                        pagination={this.state.pagination}
                        columns={columns}
                        bordered={true}
                        size="middle"
                        onChange={this.handleTableChange.bind(this)}
                    />

                </Card>
                <CareModal
                    visible={this.state.careModalVisible}
                    handleOk={(obj) => {
                        this.hidecareModal(obj)
                    }}
                    handleCancel={this.hidecareModal}
                />

                <CancelCareModal
                    visible={this.state.cancelCareModalVisible}
                    handleOk={(obj) => {
                        this.hidecancelcareModal(obj)
                    }}
                    handleCancel={this.hidecancelcareModal}
                />
            </React.Fragment>
        )
    }
}

export default withRouter(Pinkunshengchaxun)



