import React, { Component, Fragment } from 'react';
import { Select, Button, Input, Row, Col } from 'antd';
import {getAction, postAction} from "../../../axios";

const Option = Select.Option;


// const curYear = new Date().getFullYear();
// const curMonth = new Date().getMonth()+1;
// let yearList = [];
// for (let i = 0; i < 6; i++) {
//     yearList.push({
//         name: curYear - i + "年",
//         value: curYear - i + ""
//     })
// }

export default class Filter extends Component {

    state = {
        curYear1: 2018,
        curYearName1: "2018年",
        yearList: [
            {name: "2018年", value: 2018},
            {name: "2017年", value: 2017},
            {name: "2016年", value: 2016},
            {name: "2015年", value: 2015},
            {name: "2014年", value: 2014},
        ],
        //学院
        college: '',
        collegeLists: [],

        //专业
        profession: '',
        professionList: [],

        //年级
        curGrade: '',
        yearLists:[],
        // curYear: curYear + '',

        //班级
        curClass: '',
        classLists: [],

        //贫困级别
        curPinkun:'',
        curPinkunName:'全部贫困级别',
        pinkunLists:[],

        //资助类型
        curZizhu:'',
        zizhuLists:[],

        //关注
        curGuanzhu:'',
        guanzhuLists:[],

        //学号和姓名
        studentNo: '',
    }

    //获取关注list
    getGuanzhuLists () {
        getAction(
            "/bigdata/subsidizeUtil/getAttentionStatus"
        ).then((res) => {
            if (res.success) {
                this.setState({
                    curGuanzhu:'',
                    guanzhuLists: res.obj
                })
            }
        })
    }

    //获取资助类型list
    getZizhiLists () {
        getAction(
            "/bigdata/subsidizeUtil/getSubsidizeType"
        ).then((res) => {
            if (res.success){
                this.setState({
                    curZizhu:'',
                    zizhuLists: res.obj
                })
            }
        })
    }

    //获取贫困级别list
    getPinkunLists() {
        getAction(
            "/bigdata/subsidizeUtil/getPovertyLevel"
        ).then((res) => {
            if (res.success) {
                this.setState({
                    curPinkun:'',
                    pinkunLists: res.obj
                })
            }
            }
        )
    }

    // 获取学院list
    getColledgeLists() {
        postAction("/bigdata/school/listCollege").then((res) => {
            if (res.success) {
                this.setState({
                    college: '',
                    collegeLists: res.obj
                })
            }
        })
    }

    // 获取专业list
    getMajorLists() {
        postAction(
            "/bigdata/school/listMajorByCollege",
            {
                collegeCode: this.state.college
            }
        ).then(
            (res) => {
                if (res.success) {
                    this.setState({
                        profession: "",
                        professionList: res.obj
                    })
                }
            }
        )
    }

    // 获取年级list
    getGradeLists() {
        postAction(
            "/bigdata/school/getGradeList",
            {
                year: this.state.curYear1,
                collegeCode: this.state.college,
                majorCode: this.state.profession
            }
        ).then(
            (res) => {
                if (res.success) {
                    this.setState({
                        curGrade: "",
                        yearLists: res.obj
                    })
                }
            }
        )
    }

    // 获取班级list
    getClassLists() {
        postAction(
            "/bigdata/school/listClassByMajor",
            {
                year: this.state.curGrade,
                majorCode: this.state.profession
            }
        ).then(
            (res) => {
                if (res.success) {
                    this.setState({
                        curClass: "",
                        classLists: res.obj
                    })
                }
            }
        )
    }

    //筛选条件
    changeFilter = (num, value, option) => {
        if (num === 0) { // 变更自然年
            this.setState(
                {
                    curYear1: value,
                    curYearName: option.props.children,
                },
                () => {
                    this.getGradeLists()
                    this.getClassLists()
                }
            )

        } else if (num === 1) { //学院
            this.setState(
                {
                    college: value,
                    curCollegeName: option.props.children,
                },
                () => {
                    this.getMajorLists()
                    this.getGradeLists()
                    this.getClassLists()
                }
            )
        } else if (num === 2) { //专业
            this.setState(
                {
                    profession: value,
                    professionName: option.props.children,
                },
                () => {
                    this.getGradeLists()
                    this.getClassLists()
                }
            )
        } else if (num === 3) { //年级
            this.setState(
                {
                    curGrade: value,
                    // curYear: value,
                    curGradeName: option.props.children,
                },
                () => {
                    this.getClassLists()
                }
            )
        } else if (num === 4) { //班级
            this.setState({
                curClass: value,
                curClassName: option.props.children,
            })
        } else if (num === 5) {  //贫困级别
            this.setState({
                curPinkun:value,
                curPinkunName: option.props.children,
            })
        } else if (num === 6) {  //资助
            this.setState({
                curZizhu:value,
                curZizhuName: option.props.children,
            })
        } else if (num === 7) {  //关注
            this.setState({
                curGuanzhu:value,
                curGuanzhuName: option.props.children,
            })
        }
    }

    studyNum(event) {
        this.setState({ studentNo: event.target.value });
    }

    //点击搜索
    searchMore() {
        const { college, profession, curGrade, curClass, curPinkun, curGuanzhu, curZizhu,studentNo, } = this.state
        let obj = {
            curYear : this.state.curYear1,
            curCollege: college,
            curMajor: profession,
            curGrade: curGrade,
            curClass: curClass,
            curPinkun: curPinkun,
            curZizhu: curZizhu,
            curGuanzhu: curGuanzhu,
            studentNo: studentNo,
        };
        this.props.getFilterValueAndName(obj);
    }

    componentDidMount() {
        this.getColledgeLists()
        this.getMajorLists()
        this.getGradeLists()
        this.getClassLists()
        this.getPinkunLists()  // 贫困
        this.getZizhiLists()   // 资助
        this.getGuanzhuLists() // 关注
        this.searchMore()
    }

    render() {
        const { college, collegeLists, profession, professionList, curGrade, yearLists, curClass, classLists, curPinkun, pinkunLists, curZizhu, zizhuLists, curGuanzhu, guanzhuLists, studentNo } = this.state
        return (
            <Fragment>
                <div className='jzzz-top'>

                    <Row>
                        <Col span={24}>
                            <Row>
                                <Col span={2}>
                                    <span className="filter-title">选择条件：</span>
                                </Col>
                                <Col span={22}>
                                    <div className='jz-form'>
                                    <Select 
                                        value={this.state.curYear1} 
                                        onChange={this.changeFilter.bind(this, 0)}
                                    >
                                        {
                                            this.state.yearList.map((item, index) => <Option key={index} value={item.value}>{item.name}</Option>)
                                        }
                                    </Select>
                                        <Select
                                            value={college}
                                            placeholder="请选择学院"
                                            style={{ width: 180 }}
                                            onChange={this.changeFilter.bind(this, 1)}
                                        >
                                            {
                                                collegeLists.map((item, index) => (
                                                    <Option
                                                        key={index}
                                                        title={item.collegeName}
                                                        value={item.collegeCode}
                                                    >
                                                        {item.collegeName}
                                                    </Option>
                                                ))
                                            }
                                        </Select>

                                        <Select
                                            value={profession}
                                            placeholder="请选择专业"
                                            style={{ width: 150 }}
                                            onChange={this.changeFilter.bind(this, 2)}
                                        >
                                            {
                                                professionList.map((item, index) => (
                                                    <Option
                                                        key={index}
                                                        title={item.majorName}
                                                        value={item.majorCode}
                                                    >
                                                        {item.majorName}
                                                    </Option>
                                                ))
                                            }
                                        </Select>

                                        <Select
                                            value={curGrade}
                                            placeholder="请选择年级"
                                            style={{ width: 120 }}
                                            onChange={this.changeFilter.bind(this, 3)}
                                        >
                                            {
                                                yearLists.map((item, index) =>
                                                    (
                                                        <Option key={index} value={item.name}>{item.value}</Option>
                                                    ))
                                            }
                                        </Select>

                                        <Select
                                            value={curClass}
                                            placeholder="请选择班级"
                                            style={{ width: 120 }}
                                            onChange={this.changeFilter.bind(this, 4)}
                                        >
                                            {
                                                classLists.map((item, index) => (
                                                    <Option key={index} title={item.className} value={item.classCode}>{item.className}</Option>
                                                ))
                                            }
                                        </Select>

                                        <Select
                                            value={curPinkun}
                                            placeholder="全部贫困级别"
                                            style={{ width: 200 }}
                                            onChange={this.changeFilter.bind(this, 5)}
                                        >
                                            {
                                                pinkunLists.map((item, index) => (
                                                    <Option key={index} title={item.name} value={item.code}>{item.name}</Option>
                                                ))
                                            }
                                        </Select>

                                        <Select
                                            value={curZizhu}
                                            placeholder="最近资助类型"
                                            style={{ width: 200 }}
                                            onChange={this.changeFilter.bind(this, 6)}
                                        >
                                            {
                                                zizhuLists.map((item, index) => (
                                                    <Option key={index} title={item.name} value={item.code}>{item.name}</Option>
                                                ))
                                            }
                                        </Select>

                                        <Select
                                            value={curGuanzhu}
                                            placeholder="关注状态"
                                            style={{ width: 200 }}
                                            onChange={this.changeFilter.bind(this, 7)}
                                        >
                                            {
                                                guanzhuLists.map((item, index) => (
                                                    <Option key={index} title={item.name} value={item.code}>{item.name}</Option>
                                                ))
                                            }
                                        </Select>
                                        <Input placeholder="请输入学生姓名/学号" value={studentNo} onChange={this.studyNum.bind(this)} style={{ width:250, marginRight: 10 }} />

                                         <Button type="primary" icon="search" onClick={() => this.searchMore()}>搜  索 </Button>
                                    </div>
                                </Col>
                            </Row>
                        </Col>                        
                    </Row>
                </div>

            </Fragment>
        )
    }
}


